	import service from '../../service.js';
	export default {
		data() {
			return {
				emailCode:'',
				verifycode:'',
				countdown: 62,
				tabIndex: 0,
				navList: [
					{
						text: '手机号登录',
						state: '0'
					},
					{
						text: '邮箱号登录',
						state: '1'
					}
				],
				email:'',
				psd:'',
				type:2,
				account: '', //用户名
				password: '', //密码
				disabled: false,
				system: '', //获取手机的系统信息
				device_info: '' ,//设备号,后台返回,后续拿缓存的
				site_logo:'',//网站logo
				site_name:''//网站名称

			}
		},
		methods: {
			//手機驗證碼獲取倒計時
			codetime: function(obj, num) {
				if (num < 10) {
					obj.countdown = "0" + num;
				} else {
					obj.countdown = num;
				}
				num--;
				if (num < 0) {
					obj.countdown = 61;
				} else {
					setTimeout(function() {
						obj.codetime(obj, num);
					}, 1000);
				}
			},
			send() {
				var self = this
				uni.request({
					url: service.api.reg.code, 
					method: 'GET',
					data: {
						'mobile': this.account,
						'temp': 'sms_login',
					},
					success: res => {
						// console.log(res)
						var res = res.data;
						if (res.code == 1) {
							uni.showToast({
								icon: 'none',
								title: res.info
							});
							self.codetime(self, 60);
						} else {
							uni.showToast({
								icon: 'none',
								title: res.info
							});
							return;
						}
					}
				});
				// this.codetime(this,60);
			},
			emailValue(){
				var self = this
				uni.request({
					url: service.api.reg.sendEmailCode, 
					method: 'GET',
					data: {
						'email': this.email,
					},
					success: res => {
						// console.log(res)
						var res = res.data;
						if (res.code == 1) {
							uni.showToast({
								icon: 'none',
								title: res.info
							});
							self.codetime(self, 60);
						} else {
							uni.showToast({
								icon: 'none',
								title: res.info
							});
							return;
						}
					}
				});
			},
			//swiper 切换
			changeTab(e){
				this.tabClick(e.target.current);
			},
			
			//顶部tab点击
			tabClick(index){
				this.change(index);
			},
			change(index){
				this.type = this.navList[index].type;
				this.tabIndex = index;
			},
			login() {
				var self = this;
				// const reg = /^(?=.*?[0-9])(?=.*?[a-z])[0-9a-z]{8,}$/
				// if(self.password.length < 8 && !(/^(?=.*?[0-9])(?=.*?[a-z])[0-9a-z]{8,}$/.test(self.password))){
				// 	uni.showToast({
				// 		icon: 'none',
				// 		title: '请输入数字与字母结合的八位数以上密码'
				// 	})
				// 	return
				// }
				uni.request({
					url: service.api.login.login,
					method:"POST",
					data: {
						account: self.account,
						password: self.password,
						// verifycode: self.verifycode,
						type:2,
						versions:service.config.version
					},
					success(res) {
						// console.log(res, "login-------");
						if (res.data.code == 1) {
							uni.showToast({
								icon: 'none',
								title: res.data.info
							})
							var type = res.data.data.type
							var is_idverifly = res.data.data.is_idverifly
							uni.setStorageSync('type', type);
							var token = res.data.data.token
							uni.setStorageSync('token', token);
							uni.setStorageSync('tan', 1);
							uni.removeStorageSync('proCityId');
							uni.removeStorageSync('upcardata');
							// var yyy = res.data.type
							console.log(res)
						if(is_idverifly == 0 && type == "mobile"){
							setTimeout(function() {
								// uni.redirectTo({
								// 	url: '/pages/realName/real_name?type=0'
								// })
								uni.switchTab({
									url: '/pages/main/index'
								})
							}, 1000)
						}else{
						    setTimeout(function() {
							// console.log('21321321')
							uni.switchTab({
								url: '/pages/main/index'
							})
						}, 1000)
						}
						} else {
							uni.showToast({
								icon: 'none',
								title: res.data.info
							})
						}
					}

				})
			},
			// 邮箱登录
			emailLogin() {
				var self = this;
				// if(self.psd.length < 8 && !(/^(?=.*?[0-9])(?=.*?[a-z])[0-9a-z]{8,}$/.test(self.psd))){
				// 					uni.showToast({
				// 						icon: 'none',
				// 						title: '请输入数字与字母结合的八位数以上密码'
				// 					})
				// 					return
				// 				}
				uni.request({
					url: service.api.login.login,
					method:"POST",
					data: {
						email: self.email,
						password: self.psd,
						// verifycode:self.emailCode,
						type:1,
						versions:service.config.version
					},
					success(res) {
						// console.log(res, "login-------");
						if (res.data.code == 1) {
							uni.showToast({
								icon: 'none',
								title: res.data.info
							})
							// var type = res.data.data.type
							var type = res.data.data.type
							var is_idverifly = res.data.data.is_idverifly
							uni.setStorageSync('type', type);
							var token = res.data.data.token
							uni.setStorageSync('token', token);
							uni.setStorageSync('tan', 1);
							uni.removeStorageSync('proCityId');
							uni.removeStorageSync('upcardata');
							if(is_idverifly == 0 && type == "email"){
								setTimeout(function() {
									// uni.redirectTo({
									// 	url: '/pages/realName/real_name?type=1',
									// 	// ?type=1
									// })
									uni.switchTab({
										url: '/pages/main/index'
									})
								}, 1000)
							}else{
							setTimeout(function() {
								uni.switchTab({
									url: '/pages/main/index'
								})
							}, 1000)
							}
							
						} else {
							uni.showToast({
								icon: 'none',
								title: res.data.info
							})
						}
					}
			
				})
			},
			jum(url){
				uni.navigateTo({
					url:url
				})
			}
		},
		onShow() {
              uni.request({
				  url:service.api.login.logo,
				  success:(res)=>{
					  if(res.data.code == 1){
						  // console.log(res);
						  this.device_info = res.data.data.device_info;
						  this.site_name = res.data.data.site_name; 
						  this.site_logo = res.data.data.site_logo;
						  uni.setStorageSync('site_name',this.site_name);
						  uni.setStorageSync('site_logo',this.site_logo);
						  
					  }
              	 
				  }
              })
		}
	}
